<template>
    <div class="page-container">
        <el-aside class="page-nav-left">
            <div class="aside-bar">
                <el-menu default-active="1">
                    <router-link to="/monitor/index">
                        <el-menu-item index="0">
                            <i class="el-icon-video-camera"></i>
                            <span slot="title">环境监控</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/monitor/weather">
                        <el-menu-item index="1">
                            <i class="el-icon-sunny"></i>
                            <span slot="title">气象环境监测</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/monitor/soil">
                        <el-menu-item index="2">
                            <i class="el-icon-pie-chart"></i>
                            <span slot="title">土壤墒情监测</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/monitor/growth">
                        <el-menu-item index="3">
                            <i class="el-icon-cherry"></i>
                            <span slot="title">作物长势监测</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/monitor/pests">
                        <el-menu-item index="4">
                            <i class="el-icon-brush"></i>
                            <span slot="title">病虫害监测</span>
                        </el-menu-item>
                    </router-link>
                </el-menu>
            </div>
        </el-aside>
        <el-main class="m-l-220 m-r-220 m-t-20 bg-white">
            <div class="flex flex-align-center">
                <el-select class="m-r-15" size="small" v-model="vm.searchType" placeholder="颗粒度">
                    <el-option
                        v-for="item in typeList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
                <el-date-picker class="m-r-15" size="small" v-model="vm.searchDate" type="datetimerange" :picker-options="pickerOptions" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" align="right"></el-date-picker>
                <el-button type="primary" size="small" @click="search">搜索</el-button>
            </div>
        </el-main>
        <menu class="page-menu-right">
            <el-row class="m-b-30">
                <h6 class="m-b-10">请选择检测项目</h6>
                <el-select size="small" v-model="projectValue" placeholder="请选择检测项目">
                    <el-option
                        v-for="item in projectList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
            </el-row>
            <el-row class="m-b-30">
                <h6 class="m-b-10">请选择设备类型</h6>
                <el-select size="small" v-model="deviceTypeValue" placeholder="请选择设备类型">
                    <el-option
                        v-for="item in deviceTypeList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
            </el-row>
            <el-row class="m-b-15">
                <el-checkbox-group v-model="deviceValue">
                    <el-checkbox label="空气湿度1"></el-checkbox>
                    <el-checkbox label="空气湿度2"></el-checkbox>
                    <el-checkbox label="空气湿度3"></el-checkbox>
                </el-checkbox-group>
            </el-row>
        </menu>
    </div>
</template>

<script>
export default {
    data() {
        return {
            vm: {
                searchType: '',
                searchDate: '',
            },
            typeList: [

            ],
            pickerOptions: {
                shortcuts: [{
                    text: '最近一周',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '最近一个月',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '最近三个月',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                        picker.$emit('pick', [start, end]);
                    }
                }]
            },
            projectValue: '',
            deviceTypeValue: '',
            deviceValue: [],
            projectList: [
                {
                    value: 0,
                    label: '太阳辐射'
                },
                {
                    value: 1,
                    label: '降雨量'
                },
                {
                    value: 2,
                    label: '风速'
                },
                {
                    value: 3,
                    label: '大气压力'
                },
                {
                    value: 4,
                    label: '空气温度'
                },
                {
                    value: 5,
                    label: '空气湿度'
                },
                {
                    value: 6,
                    label: '光照度'
                },
                {
                    value: 7,
                    label: '风向'
                },
                {
                    value: 8,
                    label: '水面蒸发量'
                },
            ],
            deviceTypeList: [
                {
                    value: 0,
                    label: '空气湿度传感器'
                },
                {
                    value: 1,
                    label: '空气温度传感器'
                },
                {
                    value: 2,
                    label: '光照传感器'
                },
            ]
        }
    },
    computed:{
        
    },
    methods:{
        search(){

        }
        
    }
}
</script>

<style>

</style>


